import { editApi } from "../common/editApi";
import todoApi from "../common/todoApi";
import { View } from "../vendors";

export const ContentTodoListItem = View.extend({
	tagName: 'li',
	className: v => v.model.get('completed') ? 'completed' : '',
	stateClassNames: ['editing'],
	template: `
	<div class="view">
		<input class="toggle" type="checkbox" <%= checked %>>
		<label><%= text %></label>
		<button class="destroy"></button>
	</div>
	<input class="edit" value="<%= text %>">
	`,
	templateContext: v => ({ checked: v.model.get('completed') ? 'checked' : ''}),

	modelEvents: {
		'change:completed': 'update',
		'change:text': 'render',
		'edit:stop':'editStop',
		'edit:start':'editStart',
	},

	update() {
		this.$('input.toggle').checked = this.model.get('completed');
		this.updateClassName();
	},

	events: {
		'click .destroy'() {
			todoApi.remove(this.model);
		},
		'change input.toggle'(e) {
			todoApi.setCompleted(this.model, e.target.checked);
			this.update();
		},
		'dblclick > .view label'() {
			editApi.start(this.model);
		},
		'keypress input.edit'(e) {
			if (e.key !== 'Enter') { return; }
			editApi.stop(this.model);
			todoApi.change(this.model, e.target.value);
		},
		'keyup input.edit'(e) {
			if (e.key !== 'Escape') { return; }
			editApi.stop(this.model);
		}
	},

	editStart() {
		this.state('editing', true);
		this.$('input.edit').select();
	},
	editStop() {
		this.state('editing', false);
	},

});
