import todoApi from "../common/todoApi";
import { View } from "../vendors";
import { ContentTodoList } from "./content_todoList";

const template = `
<input class="toggle-all" id="toggle-all" type="checkbox">
<label for="toggle-all">Mark all as complete</label>
<ul></ul>
`;

export const Content = View.extend({
	tagName: 'section',
	className: 'main',
	template,

	initialize() {
		this.listenTo(todoApi.collection, 'update reset change', this.updateStyle);
		this.updateStyle();
	},

	updateStyle() {
		const style = !todoApi.collection.length ? 'display:none' : '';
		this.setElementAttributes({ style })
	},

	events: {
		'click .toggle-all'() {
			todoApi.toggleAll();
		}
	},

	children:{ ul: ContentTodoList }

});