import { Collection } from "../vendors";
import TodoModel from "./TodoModel";

const frameworkStorageKey = 'yaff.collection';
const text = localStorage.getItem(frameworkStorageKey);
const data = text ? JSON.parse(text) : [];

const todoCollection = new Collection(data, { model: TodoModel });

todoCollection.on('update reset change', () => {
	const jsonString = JSON.stringify(todoCollection.toJSON());
	localStorage.setItem(frameworkStorageKey, jsonString);
});

export default todoCollection