import todoApi from "../common/todoApi";
import { View } from "../vendors";
import { FooterClearButton } from "./footer_clearButton";
import { FooterCount } from "./footer_count";
import { FooterFilters } from "./footer_filters";

export const Footer = View.extend({
	class: View,
	tagName: 'footer',
	className: 'footer',

	collection: todoApi.collection,
	collectionEnabled: false,

	collectionEvents: {
		'update reset change': 'render'
	},

	updateStyle() {
		const style = !todoApi.collection.length ? 'display:none' : '';
		this.setElementAttributes({ style })
	},

	onRender() {
		this.updateStyle();
	},

	getClearButtonView() {
		if (todoApi.getCompleted().length > 0) {
			return FooterClearButton;
		}
	},

	children: [
		FooterCount,
		FooterFilters,
		v => v.getClearButtonView()
	],

});