import filtersApi from "../common/filtersApi";
import todoApi from "../common/todoApi";
import { View } from "../vendors";
import { FooterFiltersItem } from "./footer_filters_item";



export const FooterFilters = View.extend({
	className: 'filters',
	tagName: 'ul',
	
	initialize() {
		this.collection = filtersApi.collection;
	},
	modelView: FooterFiltersItem,
	childViewEvents: {
		'click'(view) {

			filtersApi.set(view.model);
			todoApi.setFilter(filtersApi.getFilterId());

		}
	}
});