import filtersApi from "../common/filtersApi";
import { View } from "../vendors";

export const FooterFiltersItem = View.extend({
	tagName: 'li',
	template: '<a href="<%= id %>"><%= text %></a>',
	onRender() {
		const selected = filtersApi.isSelected(this.model);
		this.setAClass(selected);
	},
	setAClass(selected) {
		const aEl = this.$('a');
		if (selected) {
			aEl.className = 'selected';
		} else {
			aEl.classList.remove('selected');
		}
	},
	modelEvents: {
		'select':'setAClass'
	},
	triggers: {
		click:'click'
	}
});