export { attachView } from 'yaff-view';
import { template } from 'underscore';
import { domApi } from 'yaff-core';
import { View as Base } from 'yaff-view';
import $ from 'jquery-slim';

domApi.events = Object.assign(domApi.events, {
		on(el, event, ns, selector, handler) {
			const $el = $(el);
			if (ns) {
				event += '.' + ns;
			}
			$el.on(event, selector, handler);
		},
		off(el, event, ns, selector, handler) {
			const $el = $(el);
			ns = ns ? '.' + ns : '';
			event = (event || '') + ns;
			$el.off(event, selector, handler);
		},
});

const templatesCache = new Map();

domApi.buildTemplate = arg => {
	let builded = templatesCache.get(arg);
	if (builded == null) {
		builded = template(arg);
		templatesCache.set(arg, builded);
	}
	return builded;
}

const View = Base.extend({
	// initialize() {
	// 	const name = (this.get('name') || '') + ':' + this.cid;
	// 	this.on('all', (e, view, ...rest) => console.log(`[${name}]`, e, ...rest));
	// }
});

export {
	domApi, View
}