import { Collection } from "../vendors";

const FILTERS = ['All', 'Active', 'Completed'];
const collection = buildCollection(FILTERS);

const filtersApi = {
	collection,
	selected: detectSelected(collection),
	set(model) {
		if (this.selected) {
			this.selected.trigger('select', false);
		}
		this.selected = model;
		model.trigger('select', true);		
	},
	getFilterId() {
		return this.selected?.get('text');
	},
	isSelected(model) {
		return model === this.selected;
	}
}


function buildCollection(filters) {
	const models = filters.map(text => ({ id: '#/' + (text === 'All' ? '' : text.toLowerCase()), text }));
	return new Collection(models);
}

function detectSelected(collection) {
	const id = document.location.hash || '#/';
	const selected = collection.get(id);
	if (selected) { return selected; }
}

export default filtersApi;