import todoApi from "../common/todoApi";
import { View } from "../vendors";

export const FooterCount = View.extend({
	tagName: 'span',
	className: 'todo-count',
	template: '<strong><%= amount %></strong><%= text %>',
	templateContext() {
		const amount = todoApi.getNotCompletedCount();
		const text = amount === 1 ? ' item left' : ' items left';
		return { amount, text }
	}
});