import todoApi from "../common/todoApi";
import { View } from "../vendors";
import { Content } from "./content";
import { Footer } from "./footer";
import './classic.less';


const template = `
<header class="header">
	<h1>todos</h1>
	<input class="new-todo" placeholder="What needs to be done?" autofocus="">
</header>
<section></section>
<footer></footer>`;

const Classic = View.extend({
	tagName: 'section',
	className: 'todoapp',

	template,

	children: {
		footer: Footer,
		section: Content
	},

	events: {
		'keypress input.new-todo'(e) {
			if (e.key === 'Enter' && todoApi.add(e.target.value)) {
				e.target.value = '';
			}
		}
	}

});

export default Classic;