import todoCollection from "./todoCollection";

const todoApi = {
	collection: todoCollection,
	toggleAll() {
		const hasUncompleted = this.collection.models.some(model => !model.get('completed'));
		for(let model of this.collection.models) {
			this.setCompleted(model, hasUncompleted);
		}
	},
	add(text) {
		if (!this.isValubleText(text)) { return; }
		this.collection.add({ text });
		return true;
	},
	change(model, text) {
		if (this.isValubleText(text)) {
			model.set('text', text);
		} else {
			this.remove(model);
		}
	},
	toggle(model) {
		model.set('completed', !model.get('completed'));
	},
	setCompleted(model, value) {
		model.set('completed', value);
	},
	remove(model) {
		model.destroy();
	},
	// filterId: 'All',
	setFilter(value) {
		
		const prev = this.filterId;
		if (prev === value) { return; }
		this.filterId = value;

		switch(value) {
			case 'Active':
				this.filterMethod = model => !model.get('completed');
				break;
			case 'Completed':
				this.filterMethod = model => model.get('completed');
				break;
			default:
				this.filterMethod = undefined;
				break;
		}
		
		this.collection.trigger('new:filter', this.filterMethod);
	},
	getCompleted() {
		return this.collection.where(model => model.get('completed'));
	},
	getNotCompletedCount() {
		return this.collection.length - this.getCompleted().length;
	},
	clearCompleted() {
		const models = this.getCompleted();
		this.collection.remove(models);
	},
	isValubleText(text) {
		return text && text.toString().trim() !== '';
	},

}

export default todoApi;