import todoApi from "../common/todoApi";
import { View, debounce } from "../vendors";
import { ContentTodoListItem } from "./content_todoList_item";

export const ContentTodoList = View.extend({

	tagName: 'ul',
	className: 'todo-list',
	
	collection: todoApi.collection,
	modelView: ContentTodoListItem,

	collectionEvents: {
		'new:filter':'setModelFilter',
		'change:completed':'_onChangeCompleted'
	},
	_onChangeCompleted() {
		if (!this.modelFilter) { return; }
		if (!this._handleBulkChange) {
			this._handleBulkChange = debounce(this.renderChildren.bind(this), 0);
		}
		this._handleBulkChange();
	},
	// preinitialize() {
	// 	this.on('all', e => console.warn(`view:[${e}]`));
	// 	this.collection.on('all', e => console.log(`col:[${e}]`))
	// }

});